.get-in-touch{
    background:linear-gradient(to right, #ff691f, #5ca4a9);
    height:750px;
    position: sticky;
    top:0;
    display: flex;
    align-items:center;
    justify-content: center;
}

.g-i-t-text{
    color:white;
    font-size: 65px;
}
.g-i-t-text2{
  color:white;
  text-align: center;
  padding-top: 20px;
  padding-left: 190px;
  font-size: 18px;
}

.btn--outline3:hover {
    background-color: transparent;
    color: black;
    padding: 40px 40px;
    border: 1px solid black;
    transition: all 0.3s ease-out;
    padding: 12px 26px;
    font-size: 20px;
  }

  
  .btn--outline3 {
    transition: all 0.3s ease-out;
    background: black;
    color: white;
    transition: 250ms;
    padding: 40px 40px;
    border-radius: 60px;
    border: 1px solid black;
    transition: all 0.3s ease-out;
    padding: 12px 26px;
    font-size: 20px;
  }
  
  @media (max-width: 928px) {

    .g-i-t-text2{
      color:white;
      text-align: center;
      padding-top: 20px;
      padding-left: 260px !important;
      font-size: 13px;
    }

    .g-i-t-text{
      color:white;
      font-size: 35px;
  }


  }

  @media (max-width: 1078px) {

    .g-i-t-text2{
      color:white;
      text-align: center;
      padding-top: 20px;
      padding-left: 100px;
      font-size: 13px;
    }

    .g-i-t-text{
      color:white;
      font-size: 35px;
  }

}


@media (max-width: 478px) {

  .g-i-t-text2{
    color:white;
    text-align: center;
    padding-top: 20px;
    padding-left: 0px !important;
    font-size: 13px;
  }

  .g-i-t-text{
    color:white;
    font-size: 35px;
}



}