.content-writing{
  background:linear-gradient(to right,#474a56, #5ca4a9)  ;


    height: 800px;
    position: sticky;
    top:0;
    display: flex;

      background-size: cover;
      background-position: center top;
  
}

.content-writing-header{
    height:200px;
    background-color: black;
}

.content-writing-header-2{
  height:100px;
  background-color: black;
}
.content-writing-header-text{
 color: white;
 padding-top: 70px;
 font-size: 55px;
}

#content-cc{
    display: flex;
    flex-direction: row;
    align-items: center;
    column-gap: 20px;
    justify-content: center;
}

.blink_me {
    animation: blinker 1s linear infinite;
    color:white;
  }
  
  @keyframes blinker {
    50% {
      opacity: 0;
    }
  }

  .dot {
    height: 15px;
    width: 15px;
    background-color: red;
    border-radius: 50%;
    display: inline-block;
  }

  .cc-text-container{
    display:flex ;
    flex-direction: column;
    margin-left: 00px;
    text-align: left;
  
  }

  .cc-text-1{
    color:orange;
  }

  .cc-text-2{
    color:white;
  }

  
  .btn--outline {
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border: 1px solid #fff;
    transition: all 0.3s ease-out;
    padding: 12px 26px;
    font-size: 20px;
  }

  
  .btn--outline:hover {
    transition: all 0.3s ease-out;
    background: #fff;
    color: #242424;
    transition: 250ms;
  }
  
  .hero-btns {
    margin-top: 32px;
  }
  
  #buildings:hover{
    filter: grayscale(100%);
    -webkit-filter: grayscale(100%);
    transition: all .5s ease-in-out;

  }
  @media (max-width: 767px) {

    .content-writing-header-text{
      color: white;
      padding-top: 80px;
      font-size: 35px;
     }


  }

  @media (max-width: 478px) {

  
    .content-writing-header{
      height:150px;
      background-color: black;
  }

